import { Component, Input } from '@angular/core';

/**
 * Interface for InnerTitleComponent's "data"
 *
 * @property NAME (required|options) <--- TODO: update with interface properties 
 */
export interface IInnerTitleData {
  icon?: string;
  title: {
    main: {
      text: string;
      classes?: any;
    }
    secondary?: {
      text: string;
      classes?: any;
    }
  }
  tools?: string;
  actions?: {
    select?: {
      label?: string;
      options: IInnerTitleOptions[];
      payload?: any;
    }
    search?: {
      placeholder: string;
      payload: any;
      button?: {
        text: string;
        payload: any;
      }
      _meta?: any;
    }
    buttons?: IInnerTitleButtons[];
  }
}
export interface IInnerTitleOptions {
  value: string;
  payload: any;
}
export interface IInnerTitleButtons {
  text: string;
  payload: any;
  classes?: any;
}

@Component({
  selector: 'n7-inner-title',
  templateUrl: './inner-title.html'
})
export class InnerTitleComponent {
  @Input() data: IInnerTitleData;
  @Input() emit: any;

  onClick(payload){
    if(!this.emit) return;

    this.emit('click', payload);
  };

  onChange(payload, value){
    if(!this.emit) return;

    this.emit('change', { inputPayload: payload, value })
  }

  onInputChange(payload, value){
    if(!this.emit) return;

    this.emit('change', { inputPayload: payload, value })
  }

  onInputEnter(payload, value) {
    if(!this.emit) return;
    
    this.emit('search', { inputPayload: payload, value })
  }
}