import { Component, Input } from '@angular/core';

/**
 * Interface for AdvancedAutocompleteComponent's "data"
 *
 * @property NAME (required|options) <--- TODO: update with interface properties 
 */
export interface IAdvancedAutocompleteData {
  results?: IAdvancedAutocompleteResponse[];
  actions?: {
    advanced?: {
      text: string;
      payload: any;
      classes?: any;
    }
    showMore?: {
      text: string;
      payload: any;
      classes?: any;
    }
  }
  fallback: string;
  classes?: any;
  _meta?: any;
}
export interface IAdvancedAutocompleteResponse {
  /**
   * group of results with a title
   * NOTE: Nested groups WILL NOT BE RENDERED!
   */
  group?: {
    title: string;
    items: IAdvancedAutocompleteResponse[];
    classes?: string;
    icon?: string;
    payload?: any;
  }
  label?: string;
  value?: string;
  payload?: any;
}

@Component({
  selector: 'n7-advanced-autocomplete',
  templateUrl: './advanced-autocomplete.html'
})
export class AdvancedAutocompleteComponent {
  @Input() data: IAdvancedAutocompleteData;
  @Input() emit: any;

  onClick(payload){
    if(!this.emit) return;

    this.emit('click', payload);
  }
}